import React from "react"
import { Link, withPrefix } from "gatsby"

const extlink = function (path) {
    if (path.startsWith('http')) {
        return path;
    } else {
        return withPrefix(path);
    }
}

export default ({slug}) => (
  <>

    <video id="my-video" controls preload="auto" width="720" data-setup='{"playbackRates": [0.5,1,1.5,2]}'>
      <source type="video/mp4" src={"https://barnabas-park.s3.amazonaws.com/" + slug + ".mp4"}></source>
    </video>

  </>
)
