import React from "react"
import { graphql, withPrefix } from "gatsby"
import Layout from "../components/layout"
// import { MDXProvider, MDXRenderer } from "gatsby-plugin-mdx"
import MDXRenderer from "gatsby-plugin-mdx/mdx-renderer"
import { MDXProvider } from "@mdx-js/react"

import Sc from "./sc"
import Video from "./video"
import Alert from "./alert"

const extlink = function (l) {
    if (l.startsWith('http')) {
        return l;
    } else {
        return withPrefix(l);
    }
}

const shortcodes = {Sc,Video,Alert}
export default function PageTemplate({ data: { mdx } }) {
  return (
    <Layout>
      <h1>{mdx.frontmatter.title}</h1>
          <MDXProvider components={shortcodes}>
      <MDXRenderer frontmatter={mdx.frontmatter}>{mdx.body}</MDXRenderer></MDXProvider>
          { typeof mdx.frontmatter.links === "undefined" ||
            (mdx.frontmatter.links &&
             mdx.frontmatter.links.map(cat =>
                                      <>
                                      <h2> {cat.category} </h2>
                                      <ul>
                                      {cat.links.map(links =>
                                                     <li> <a href={extlink(links.path)}>{links.title}</a> {links.desc} </li>
                                      )}
                                      </ul>
                                      </>
          ))}
    </Layout>
  )
}

export const pageQuery = graphql`
  query ContentQuery($id: String) {
    mdx(id: { eq: $id }) {
      id
      body
      frontmatter {
        title
        links { category links { title path desc }}
      }
    }
  }
`
